* {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}
.app-container {
    display: flex;
    justify-content: flex-start;
    height: calc(100% - 56px);
}

.app-container-panel {
    height: 100%;
    width: 60px;
}

.app-container-content {
    height: 100%;
    width: calc(100% - 60px);
}
.header {
    height: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.header::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 100%;
    height: 4px;
    background: linear-gradient(rgba(9, 30, 66, 0.13) 0px, rgba(9, 30, 66, 0.13) 1px, rgba(9, 30, 66, 0.08) 1px, rgba(9, 30, 66, 0) 4px);
}

.header-title {
    font-size: 24px;
}
.news {
    width: 100%;
    margin-top: 12px;
}

.news-container {
    padding-left: 12px;
    padding-right: 12px;
    overflow-x: hidden;
    height: 100%;
}

.news-test {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    border: 1px solid rgb(221, 232, 241);
    padding-left: 12px;
    padding-right: 12px;
    margin-bottom: 12px;
}

.news-test-author-image {
    width: 80px;
    height: 80px;
    border-radius: 40px;
    margin-right: 16px;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}

.news-test-author {
    display: flex;
    align-items: center;
}

.news-test-author-info {
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news-test-author-info-name {
    font-weight: bold;
}

.news-test-author-info-position {
    color: #6b778c;
}

.news-test-info-name-content {
    font-style: italic;
    font-size: 14px;
    font-weight: bold;
    color: #00D8F6;
    margin-top: 4px;
}

.news-test-reporter {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 60px;
    font-size: 14px;
    color: #6b778c;
}

.news-test-reporter-name {
    margin-top: 15px;
}

.news-test-info,
.news-feedback-info {
    display: flex;
    flex-direction: column;
    height: 56px;
    width: 400px;
}

.news-feedback-time {
    font-size: 14px;
    color: #6b778c;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 60px;
}

.news-test-button-link,
.news-feedback-button-link {
    text-decoration: none;
    padding: 8px 16px 8px 16px;
    color: #00D8F6;
    font-style: italic;
    font-weight: bold;
}

.news-feedback-button,
.news-test-button {
    border: 2px solid #00D8F6;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.tasks {
    width: calc(100% - 24px);
    margin: 0 auto;
    height: 100%;
}

.tasks-title {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;
}

.tasks-date {
    color: #00D8F6;
    font-weight: bold;
}

.tasks-list {
    overflow-x: hidden;
    height: calc(100% - 48px);
}

.tasks-list-item {
    margin-bottom: 24px;
    color: rgb(0, 28, 61);
}

.tasks-list-item-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.tasks-list-item-title-name.normal {
    color: #FFCC00;
}

.tasks-list-item-title-name.bad {
    color: #FF3300;
}

.tasks-list-item-title-name.well {
    color: #33FF66;
}

.tasks-list-item-info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.tasks-list-item-info-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    font-size: 12px;
}

.tasks-list-item-title {
    margin-bottom: 8px;
}

.tasks-list-item-title-duration {
    font-size: 14px;
    color: #00D8F6;
}

.tasks-list-item-info-description {
    font-style: italic;
    font-size: 14px;
    margin-bottom: 4px;
}
.schedule {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    position: relative;
    border-left: 1px solid rgb(221, 232, 241);
    width: 370px;
}

/* .schedule-container {
    height: 40%;
    min-height: 500px;
} */

.schedule-month {
    display: flex;
    flex-direction: column;
}

.schedule-month-day {
    cursor: pointer;
    width: 45px;
    text-align: center;
}

.schedule-month-week {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    font-size: 24px;
    margin-bottom: 8px;
}

.schedule-month-title-name {
    text-align: center;
    margin: 24px 0px 24px 0px;
}

.schedule-month-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 12px;
    margin-right: 8px;
    color: #00D8F6;
}

.schedule-month-title-year {
    color: #00D8F6;
    font-weight: bold;
}

.schedule-navigator {
    display: flex;
    justify-content: space-between;
    width: calc(100% - 24px);
    margin: 0 auto;
}

.schedule-navigator-trigger {
    margin-top: 16px;
    color: #00D8F6;
    font-weight: bold;
    cursor: pointer;
    border-bottom: 2px solid transparent;
    transition: .3s;
}

.schedule-navigator-trigger:hover {
    border-bottom: 2px solid #00D8F6;
}

.schedule-tasks { 
    margin-top: 48px;
    height: calc(100% - 340px);
}
.home-container {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
}
.leftPanel {
    background-color: rgb(240, 244, 247);
    height: 100%;
    box-shadow: rgb(221 232 241) 1px 0px 0px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.leftPanel-list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    list-style-type: none;
}

.leftPanel-list-item {
    width: 28px;
    height: 28px;
    margin-bottom: 24px;
    cursor: pointer;
}

.leftPanel-list-item.home {
    margin-top: 24px;
    width: 32px;
    height: 32px;
}

.leftPanel-list-item-image {
    width: 100%;
    height: 100%;
}

.leftPanel-profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.leftPanel-profile-icon {
    width: 48px;
    height: 48px;
    margin-bottom: 12px;
}

.leftPanel-profile-status {
    width: 32px;
    height: 32px;
    margin-bottom: 20px;
}

.leftPanel-profile-icon-image, .leftPanel-profile-status-image {
    width: 100%;
    border-radius: 90px;
}
.tooltip {
    position: relative;
}

.tooltip-content {
   
}

.tooltip:hover .tooltip-content {
    -webkit-animation: appearance-animation .2s forwards;
            animation: appearance-animation .2s forwards;
}

.tooltip-content {
    align-items: center;
    justify-content: center;
    position: absolute;
    left: calc(100% + 14px);
    top: 0px;
    border: 1px solid rgb(240, 244, 247);
    background-color: rgb(240, 244, 247);
    border-radius: 0px 5px 5px 0px;
    padding: 8px 12px 8px 12px;
    color: #6b778c;
    -webkit-animation: disappearance-animation .2s forwards;
            animation: disappearance-animation .2s forwards;
}
