@-webkit-keyframes appearance-animation {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
        display: flex;
    }

    50% {
        opacity: .5;
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes appearance-animation {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
        display: flex;
    }

    50% {
        opacity: .5;
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@-webkit-keyframes disappearance-animation {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }

    50% {
        opacity: .5;
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

    100% {
        opacity: 0;
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
        display: none;
    }
}

@keyframes disappearance-animation {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }

    50% {
        opacity: .5;
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

    100% {
        opacity: 0;
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
        display: none;
    }
}
.testsPage-navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 58px;
    background-color: rgb(240, 244, 247);
}

.testsPage-navbar-item {
    list-style-type: none;
    margin-right: 24px;
    font-weight: bold;
    border-bottom: 2px solid transparent;
    transition: .2s;
    cursor: pointer;
}

.testsPage-navbar-item.active {
    color: #00D8F6;
    border-bottom: 2px solid #00D8F6;
}

.testsPage-navbar-item:last-child {
    margin-right: 0px;
}

.testsPage-container {
    margin-left: 1px;
}
